<template>
  <div>
    <div v-if="work.image">
      <img :src="api_url + work.image.url" />
    </div>
    <h1 v-if="work.title">{{ work.title }}</h1>

    <div>
      <vue-markdown-it
        v-if="work.content"
        :source="work.content"
      ></vue-markdown-it>
    </div>
  </div>
</template>

<script>
import VueMarkdownIt from "vue-markdown-it";
import gql from "graphql-tag";

export default {
  data() {
    return {
      work: {},
      api_url: process.env.VUE_APP_STRAPI_API_URL,
      routeParam: this.$route.params.id,
    };
  },
  components: {
    VueMarkdownIt,
  },
  apollo: {
    work: {
      query: gql`
        query Works($id: ID!) {
          work(id: $id) {
            id
            title
            content
            image {
              url
            }
          }
        }
      `,
      variables() {
        return { id: this.routeParam };
      },
    },
  },
};
</script>
