<template>
  <div>
    <h1>Contact</h1>
    <p v-if="contact.content">
      {{ contact.content }}
    </p>
  </div>
</template>

<script>
import gql from "graphql-tag";

export default {
  data() {
    return {
      contact: {},
    };
  },
  apollo: {
    contact: {
      query: gql`
        query a {
          contact {
            content
          }
        }
      `,
    },
  },
};
</script>
