import Vue from "vue";
import VueApollo from "vue-apollo";
import VueRouter from "vue-router";

import apolloClient from "./vue-apollo";

import App from "./App.vue";

// Require the main Sass manifest file
require("./assets/sass/main.scss");

Vue.use(VueApollo);
Vue.use(VueRouter);
Vue.config.productionTip = false;

const apolloProvider = new VueApollo({
  defaultClient: apolloClient,
});

const router = new VueRouter({
  mode: "history",
  routes: [
    {
      path: "/",
      components: require("./containers/Articles.vue"),
    },
    {
      path: "/article/:id",
      components: require("./containers/Article.vue"),
    },
    {
      path: "/category/:id",
      components: require("./containers/Category.vue"),
    },
    {
      path: "/works/",
      components: require("./containers/Works.vue"),
    },
    {
      path: "/work/:id",
      components: require("./containers/Work.vue"),
    },
    {
      path: "/contact",
      components: require("./containers/Contact.vue"),
    },
    {
      path: "/about",
      components: require("./containers/About.vue"),
    },
  ],
});

new Vue({
  apolloProvider,
  router,
  render: (h) => h(App),
}).$mount("#app");
