<template>
  <div>
    <h1>ABOUT</h1>
    <img v-if="about.image" :src="api_url + about.image.url" />
    <p v-if="about.content">
      {{ about.content }}
    </p>
  </div>
</template>

<script>
import gql from "graphql-tag";

export default {
  data() {
    return {
      about: {},
      api_url: process.env.VUE_APP_STRAPI_API_URL,
    };
  },
  apollo: {
    about: {
      query: gql`
        query About {
          about {
            content
            image {
              url
            }
          }
        }
      `,
    },
  },
};
</script>
