      </div>
      <div>
        <p v-if="article.category">
          {{ article.category.name }}
        </p>
        <p>{{ article.title }}</p>
      </div>
    </router-link>
  </div>
</template>

<script>
export default {
  data: function() {
    return {
      api_url: process.env.VUE_APP_STRAPI_API_URL,
    };
  },
