<template>
  <div>
    <router-link
      v-for="work in works"
      :to="{ path: '/work/' + work.id }"
      :key="work.id"
    >
      <div v-if="work.image">
        <img :src="api_url + work.image.url" alt="" />
      </div>
      <div v-if="work.title">
        <p>{{ work.title }}</p>
      </div>
    </router-link>
  </div>
</template>

<script>
import gql from "graphql-tag";

export default {
  data() {
    return {
      works: {},
      api_url: process.env.VUE_APP_STRAPI_API_URL,
    };
  },
  apollo: {
    works: gql`
      query Works {
        works {
          id
          title
          content
          image {
            url
          }
        }
      }
    `,
  },
};
</script>
